body {
  background-color: #1c1c1e; /* Dark background for the entire page */
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
}

.calculator {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  max-width: 300px;
  margin: 50px auto;
  border-radius: 20px;
  padding: 20px;
  background-color: #2c2c2e; /* Dark background for the calculator */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5); /* Adding some depth */
}

button {
  padding: 20px;
  font-size: 18px;
  border: none;
  border-radius: 10px;
  background-color: #3a3a3c;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #505050;
}

button:active {
  background-color: #6c6c6e;
}

#display {
  grid-column: span 4;
  background-color: #2c2c2e;
  padding: 20px;
  text-align: right;
  font-size: 24px;
  border-radius: 10px;
  box-shadow: inset 0 4px 10px rgba(0, 0, 0, 0.5);
}
